import React, {Component} from 'react';
import {Link} from "react-router-dom";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';

const codeurl = 'https://satiated-toe.000webhostapp.com/code.php'
const produrl = 'https://satiated-toe.000webhostapp.com/product.php'

export default class Admin extends Component {
	constructor(props){
		super(props);

		this.getcodes = this.getcodes.bind(this);
		this.getproducts = this.getproducts.bind(this);
	}

	state = {
		codes: [],
		products: []
	}

	getcodes() {
		fetch(codeurl, {
			method: 'POST',
			body: JSON.stringify({
				action: 'getall'
			})
		})
		.then(res => res.json())
		.then((data) => {
			this.setState({
				codes: data.codes
			});
		})
		.catch(error => {
			console.log(error);
		});
	}

	getproducts() {
		fetch(produrl, {
			method: 'POST',
			body: JSON.stringify({
				action: 'getall'
			})
		})
		.then(res => res.json())
		.then((data) => {
			this.setState({
				products: data.products
			});
		})
		.catch(error => {
			console.log(error);
		});
	}

	componentDidMount(){
		this.getcodes();
		this.getproducts();
	}

	render() {
		return (
			<div className="App">
				<header className="page-header">
					<Link to="/">
						<input type="button" class="homebutton" value="Home"/>
					</Link>
					<div class="space"/><div class="space"/><div class="space"/>
					<img src={process.env.PUBLIC_URL+'/0.png'} className="logo"/>
				</header>
				<Tabs>
					<TabList>
						<Tab>Products</Tab>
						<Tab>Codes</Tab>
					</TabList>

					<TabPanel>
						<div className="panel">
							<table className="table">
								<tr>
									<th>ID</th>
									<th>Name</th>
									<th>Price</th>
									<th>Detail</th>
									<th>Config</th>
								</tr>
								{this.state.products.map(product => (
									<tr>
										<td>{product.ID}</td>
										<td>{product.Name}</td>
										<td>{product.Price}</td>
										<td>{product.Detail}</td>
										<td>
											<button>Edit</button>
											<button>Delete</button>
										</td>
									</tr>
								))}
							</table>
						</div>
					</TabPanel>
					<TabPanel>
						<div className="panel">
							<table className="table">
								<tr>
									<th>Code</th>
									<th>Value</th>
									<th>Expired Date</th>
									<th>Config</th>
								</tr>
								{this.state.codes.map(code => (
									<tr>
										<td>{code.Code}</td>
										<td>{code.Value}</td>
										<td>{code.Date}</td>
										<td>
											<button>Edit</button>
											<button>Delete</button>
										</td>
									</tr>
								))}
							</table>
						</div>
					</TabPanel>
				</Tabs>
				
		    </div>
		);
	}
}