import React, {Component} from 'react';
import {Switch, Route, Link} from "react-router-dom";
import './App.css';
import './Style.css';
import Admin from './Components/Admin';
import User from './Components/User';


class App extends Component {
  render(){
    return (
      <div>
        <Switch>
          <Route exact path="/Admin"> 
            <Admin />
          </Route>
          <Route exact path="/User"> 
            <User />
          </Route>
          <Route path="/"> 
            <Home />
          </Route>
        </Switch>
      </div>
    );
  }
}

function Home() {
  return(
    <div className="App">
      <header className="App-header">
        <img src={process.env.PUBLIC_URL+'/0.png'} className="App-logo"/>
        <p>
          Who you are?
        </p>
        <div>
          <Link to="/Admin">
            <input type="button" className="button" value="Admin"/>
          </Link>
          <div className="space"/>
          <Link to="/User">
            <input type="button" className="button" value="User"/>
          </Link> 
        </div>
      </header>
    </div>
  )
}

export default App;
